import { BasePage } from './../base/base';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ActionSheetController, AlertController } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';

/**
 * Generated class for the TaskImplementPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-task-implement',
  templateUrl: 'task-implement.html',
})
export class TaskImplementPage extends BasePage {

  aa: any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public camera: Camera,
    public actionSheetCtrl: ActionSheetController,
    public alertCtrl: AlertController) {
    super(navCtrl, navParams, toastCtrl);
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TaskImplementPage');
  }
  preservation() {

  }
  addImg() {
    let actionSheet = this.actionSheetCtrl.create({
      buttons: [{
        text: '拍照',
        role: 'takePhoto',
        handler: () => {
          this.takePhoto();
        }
      }, {
        text: '从相册选择',
        role: 'chooseFromAlbum',
        handler: () => {
          this.chooseFromAlbum();
        }
      }, {
        text: '取消',
        role: 'cancel',
        handler: () => {
          console.log("cancel");
        }
      }]
    });

    actionSheet.present().then(value => {
      return value;
    });

  }

  // 照相
  takePhoto() {
    const options: CameraOptions = {
      //图片质量，越高质量越好（0~100）
      quality: 100,
      //允许编辑
      allowEdit: false,
      targetWidth: 200,
      targetHeight: 200,
      //是否允许保存到相册
      saveToPhotoAlbum: true,
      //图片路径
      destinationType: this.camera.DestinationType.DATA_URL,
      //媒体类型，图片、视频等
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.CAMERA,
    };

    this.camera.getPicture(options).then(imageURI => {
      let base64Image = 'data:image/jpeg;base64,' + imageURI;
      this.aa = base64Image;

      alert(base64Image);
    }, error => {
      console.log('Error: ' + error);
      this.toast("获取图片失败！");
    });
  }
  // 从相册中选择
  chooseFromAlbum() {
    const options: CameraOptions = {
      //图片质量，越高质量越好（0~100）
      quality: 100,
      //允许编辑
      allowEdit: false,
      targetWidth: 200,
      targetHeight: 200,
      //是否允许保存到相册
      saveToPhotoAlbum: true,
      //图片路径DATA_URL,FILE_URI
      destinationType: this.camera.DestinationType.DATA_URL,
      //媒体类型，图片、视频等
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
    };

    this.camera.getPicture(options).then(imageURI => {
      let base64Image = 'data:image/jpeg;base64,' + imageURI;
      alert(base64Image);
      this.aa = base64Image;
      console.log('Image URI: ' + imageURI);
    }, error => {
      console.log('Error: ' + error);
      this.toast("获取图片失败！");
    });
  }

  delImg(loc) {
    let alert = this.alertCtrl.create({
      title: "删除提示",
      message: "你确定删除该图片？",
      buttons: [
        {
          text: "取消"
        },
        {
          text: "确定",
          handler: () => {

          }
        }
      ]
    });
    alert.present();
  }
}



